<template>
<MyDialog width="500" title="Редактировать срок действия">
  <template v-slot:activator="{ on, attrs }">
    <v-col cols="12" sm="6">
      <v-card v-bind="attrs" v-on="on">
        <v-list-item three-line>
          <v-list-item-content>
            <div class="overline mb-4">Срок действия</div>
            <v-list-item-title class="headline mb-1">{{service.createdate}}</v-list-item-title>
            <v-list-item-subtitle>{{service.expiredate}}</v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-avatar tile size="80">
            {{days}} дней осталось
          </v-list-item-avatar>
        </v-list-item>
        <v-list-item two-line>
          <v-list-item-content>
            <div class="overline mb-4">Продление</div>
            <v-list-item-title class="headline mb-2">{{ {
              [-50]: 'День',
              [1]: 'Месяц',
              [3]: '3 месяца',
              [6]: '4 месяца',
              [12]: 'Год',
              [0]: 'Вечный'
            }[service.costperiod] }}</v-list-item-title>
            <v-list-item-subtitle v-if="service.scheduledclose === 'on'" style='color: red'>Запланировано удаление</v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-avatar tile size="80">{{service.cost}}₽</v-list-item-avatar>
        </v-list-item>
      </v-card>
    </v-col>
  </template>
  <v-container>
    <v-text-field v-model="expiredate" label="Дата окончания">
      <template v-slot:append>
        <v-btn icon @click="setNearDay"><v-icon>mdi-timeline-clock</v-icon></v-btn>
        <v-btn icon @click="() => tabMgr.open('RefundCalculator', { endDate: expiredate, price: service.cost })">
          <v-icon>mdi-calculator</v-icon>
        </v-btn>
      </template>
    </v-text-field>
    <v-checkbox v-model="scheduledclose" label="Запланировано удаление"/>
    <v-btn @click="save" :loading="loading" :disabled="!correct" color="primary">Сохранить</v-btn>
  </v-container>
</MyDialog>
</template>

<script>
import MyDialog from '@/render/components/Common/MyDialog';

export default {
  components: { MyDialog },
  props: ['service', 'type'],
  data: () => ({
    loading: false,
    expiredate: '2020-01-01',
    scheduledclose: false
  }),

  mounted() {
    this.expiredate = this.service.expiredate;
    this.scheduledclose = this.service.scheduledclose === 'on';
  },

  methods: {
    async save() {
      this.loading = true;
      this.service.expiredate = this.expiredate;
      this.service.scheduledclose = this.scheduledclose ? 'on' : 'off';
      await this.api.func(`${this.type}.edit`, {
        sok: 'ok',
        clicked_button: 'ok',
        expiredate: this.service.expiredate,
        scheduledclose: this.service.scheduledclose,
        elid: this.service.elid
      }).catch(v => {
        // console.log(v)
        alert(v.message);
      });

      this.loading = false;
      alert('Успешно');
    },

    setNearDay() {
      const newDate = new Date(Date.now() - new Date().getTimezoneOffset() + 3600e3 * 3 + 86400e3);
      this.expiredate = `${newDate.getFullYear()}-${newDate.getMonth() + 1}-${newDate.getDate()}`;
    }
  },

  computed: {
    correct() {
      return !Number.isNaN(Date.parse(this.expiredate));
    },

    days() {
      return Math.floor((Date.parse(this.service.expiredate) - Date.now()) / 86400e3);
    }
  }
};
</script>
