<template>
<v-col cols="12" sm="6">
  <MyDialog width="900" title="Инструкция к серверу" :loading="!data">
    <template v-slot:activator="{ on, attrs }">
      <v-card v-bind="attrs" v-on="on">
        <v-card-title>Инструкция к серверу</v-card-title>
      </v-card>
    </template>
    <v-card-text style="white-space: pre-wrap;">{{data}}</v-card-text>
  </MyDialog>
</v-col>
</template>

<script>
import MyDialog from '@/render/components/Common/MyDialog';
import createTextVersion from 'textversionjs';

export default {
  components: { MyDialog },
  props: ['service'],

  methods: {
    decodeHTMLEntities(text) {
      const entities = [
        ['amp', '&'],
        ['apos', '\''],
        ['#x27', '\''],
        ['#x2F', '/'],
        ['#39', '\''],
        ['#47', '/'],
        ['lt', '<'],
        ['gt', '>'],
        ['nbsp', ' '],
        ['quot', '"']
      ];

      for (let i = 0, max = entities.length; i < max; ++i) { text = text.replace(new RegExp(`&${entities[i][0]};`, 'g'), entities[i][1]); }

      return text;
    }
  },

  asyncComputed: {
    async data() {
      const instruction = await this.api.func('service.instruction.html', { elid: this.service.elid });
      return createTextVersion(this.api.decodeHTMLEntities(instruction.body));
    }
  }
};
</script>
