<template>
<TabContent v-if="service">
  <AppBar :service="service" :type="type"/>
  <v-row style="padding: 10px; overflow-y: auto; max-height: 100%">
    <ExpireDate :service="service" :type="type"/>
    <Instruction :service="service"/>
    <DomainCard :service="service" :type="type" @update="load"/>
  </v-row>
  <template v-slot:rightSidebar>
    <Actions :service="service" :type="type" @update="load"/>
  </template>
</TabContent>
<TabContentLoading v-else :error="error"/>
</template>

<script>
import TabContent from '@/render/components/Common/TabContent.vue';
import TabContentLoading from '@/render/components/Common/TabContentLoading.vue';
import AppBar from '@/render/components/Service/AppBar.vue';
import ExpireDate from '@/render/components/Service/ExpireDate.vue';
import Actions from '@/render/components/Service/Actions.vue';
import Instruction from '@/render/components/Service/Instruction.vue';
import DomainCard from '@/render/components/Service/DomainCard.vue';

export default {
  components: {
    TabContent, TabContentLoading, AppBar, ExpireDate, Actions, Instruction, DomainCard
  },
  props: ['value'],
  data: () => ({
    service: null,
    type: null,
    loading: false,
    error: null
  }),

  async mounted() {
    if (this.value.saved) {
      this.type = this.value.saved.type;
      this.service = this.value.saved.service;
    } else {
      await this.load();
    }

    this.value.saved = { service: this.service, type: this.type };
    this.applyData();
  },

  methods: {
    async load() {
      this.loading = true;
      this.service = await this.tkb.query('get', `billing/service/${this.value.query.elId}`)
        .catch(v => { this.error = v; });

      this.type = this.service.type;

      this.loading = false;
    },

    async applyData() {
      this.value.title = `S${this.value.query.elId}`;
      this.value.color = 'green';
      if (!this.service) {
        return;
      }

      this.value.fullTitle = this.service.name;
      window.service = this.service; // Debug
    },

    async openWindow() {
      const goto = await this.api.exec('gotoserver', { elid: this.service.elid }).catch(() => false);
      if (!goto) {
        alert('У этой услуги нет панели управления или она не работает.');
      }

      this.platform.openPanelWindow(goto.ok, this.service.elid);
    }
  }
};
</script>
