<template>
  <v-col cols="12" sm="6">
    <v-card>
      <v-card-title>
        <v-text-field
          :loading="loading"
          style="width: 100%"
          dense
          hide-details
          v-if="isEdit"
          v-model="value"
          @keyup.enter="apply"
          @keyup.escape="isEdit = false"
        />
        <v-btn v-else text @click="edit">{{service.domain}}</v-btn>
      </v-card-title>
    </v-card>
  </v-col>
</template>

<script>
export default {
  props: ['service', 'type'],
  data: () => ({
    loading: false,
    isEdit: false,
    value: ''
  }),

  methods: {
    edit() {
      this.value = this.service.domain;
      this.isEdit = true;
    },

    async apply() {
      this.loading = true;
      await this.api.func(`${this.type}.edit`, {
        sok: 'ok',
        clicked_button: 'ok',
        domain: this.value,
        elid: this.service.elid
      }).catch(v => {
        alert(v.message);
      }).finally(() => {
        this.loading = false;
        this.isEdit = false;
      }).then(() => {
        this.$emit('update');
      });
    }
  }
};
</script>
