<template>
<v-dialog :width="width || '700'" scrollable v-model="isActive" :fullscreen="core.mobile">
  <template v-slot:activator="{ on, attrs }">
    <slot name="activator" v-bind="{ attrs, on }"/>
  </template>
  <v-container color="primary" dark v-if="!!loading"><v-row class="fill-height" align-content="center" justify="center">
  <v-col cols="6">
    <v-card>
      <v-card-text>
    <v-progress-linear
      color="deep-purple accent-4"
      indeterminate
      rounded
      height="6"
    />
      </v-card-text>
    </v-card>
  </v-col>
</v-row></v-container>
  <v-card v-else>
    <v-card-title dark style="margin-bottom: 10px">
      <v-toolbar-title style="width: calc(100% - 48px)" v-if="$slots.title"><slot name="title"/></v-toolbar-title>
      <v-toolbar-title style="width: calc(100% - 48px)" v-else>{{title}}</v-toolbar-title>
      <v-spacer/>
      <v-btn icon dark @click="isActive = false" style="float: right"><v-icon>mdi-close</v-icon></v-btn>
    </v-card-title>
    <slot/>
  </v-card>
</v-dialog>
</template>

<script>
export default {
  props: ['loading', 'width', 'title', 'value'],
  data: () => ({
    isActive: false
  }),

  mounted() {
    this.isActive = !!this.value;
  },

  watch: {
    isActive() {
      this.$emit('input', this.isActive);
    }
  }
};
</script>
