<template>
<v-app-bar dense class="cards" style="z-index: 1">
  <v-toolbar-title><a class="a-title" target="__blank" :href="`https://my.msk.host/billmgr?fiter=on&elid=${service.id}&startform=${type}.edit`">{{service.name}}</a></v-toolbar-title>
  <v-spacer/>
  <div>
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-icon v-bind="attrs" v-on="on">mdi-{{status[1]}}</v-icon>
      </template>
      <span>{{status[0]}}</span>
    </v-tooltip>
  </div>
</v-app-bar>
</template>

<script>
export default {
  props: ['service', 'type'],
  data: () => ({
    statusList: {
      1: ['Заказан', 'widgets'],
      2: ['Активна', 'lightbulb'],
      3: ['Остановлена', 'block-helper'],
      4: ['Удалена', 'trash'],
      5: ['Обрабатывается', 'crane']
    }
  }),

  computed: {
    status () {
      return this.statusList[this.service.status] || [`UNKNOWN ${this.service.status}`, 'help']
    }
  }
}
</script>
